export const initArrays = {
  initOpenArray:[false,false,false,false,false,false,false,false,false,false,false,false,false,false,false,false],
  fullOpenArray:[true,true,true,true,true,true,true,true,true,true,true,true,true,true,true,true],
  initBorderArray:[border.NONE,border.NONE,border.NONE,border.NONE,border.NONE,border.NONE,border.NONE,border.NONE,border.NONE,border.NONE,border.NONE,border.NONE,border.NONE,border.NONE,border.NONE],
  initCardArray:[cards.dokidoki,cards.dolly,cards.konjo,cards.maze,cards.melody,cards.natsu,cards.pti,cards.xmas,cards.dokidoki2,cards.dolly2,cards.konjo2,cards.maze2,cards.melody2,cards.natsu2,cards.pti2,cards.xmas2]
}

export const initialState = {
  message:messages.START,
  count: 0,
  intervalID: -1,
  modalIsOpen: false,
  gameStarted: false,
  startBtnOpen: "block",
  okBtnOpen: "none",
  resetBtnOpen: "none",
  okModalOpen: "none",
  clearModalOpen: "none",
  missModalIsOpen: false,
  selected: false,
  selectedNum: 0,
  selectedNum2: 0,
  okCount: 0,
  yukimiArray:initArrays.initCardArray,
  yukimiOpenArray:initArrays.initOpenArray,
  yukimiBorderArray:initArrays.initBorderArray,
  card:cards.normal,
  tweet:"「雪美あわせ」を遊びました！",
  hashtags:["第7回シンデレラガール総選挙","佐城雪美","雪美あわせ"],
}

// State がundefinedの場合はデフォルト引数でinitialStateを使用するようにする
export function reducer(state, action){
    switch(action.type) {
      case types.GAME_START:
        return Object.assign({}, state, {startBtnOpen: "none",okBtnOpen: "block",intervalID: action.intervalID,yukimiOpenArray:initArrays.fullOpenArray,yukimiArray:action.yukimiArray,message:messages.NEXT});
      case types.UPDATE_TIMER:
        return Object.assign({}, state, {count: state.count + 1});
      case types.GAME_RESET:
        clearInterval(state.intervalID)
        return Object.assign({}, state, {yukimiArray:initArrays.initCardArray.slice(),
                                         okModalOpen: "none",
                                         resetBtnOpen: "none",
                                         okCount:0,
                                         gameStarted:false,
                                         okMdalOpen: "none",
                                         clearModalOpen: "none",
                                         startBtnOpen: "block",
                                         count:0,
                                         yukimiOpenArray:initArrays.initOpenArray.slice(),
                                         yukimiBorderArray:initArrays.initBorderArray.slice(),
                                         selected: false,
                                         message:messages.START,
                                         card:cards.normal});
      case types.ONE_SELECTED:
        return Object.assign({}, state, {okModalOpen: "none",selected: action.selected,selectedNum: action.selectedNum,yukimiOpenArray:action.yukimiOpenArray,yukimiBorderArray:action.yukimiBorderArray,message:messages.SELECTED});
      case types.OK:
        return Object.assign({}, state, {okModalOpen: "block",okCount: state.okCount + 1,selected: action.selected,selectedNum: action.selectedNum,yukimiOpenArray:action.yukimiOpenArray,yukimiBorderArray:action.yukimiBorderArray,message:messages.OK,card:action.card});
      case types.MISS:
        return Object.assign({}, state, {count: state.count + 3,missModalIsOpen: action.missModalIsOpen,selected: action.selected,selectedNum: action.selectedNum,selectedNum2: action.selectedNum2,yukimiOpenArray:action.yukimiOpenArray,yukimiBorderArray:action.yukimiBorderArray,message:messages.MISS});
      case types.CLEAR:
        clearInterval(state.intervalID)
        return Object.assign({}, state, {clearModalOpen: "block",okCount: state.okCount + 1,selected: action.selected,selectedNum: action.selectedNum,yukimiOpenArray:action.yukimiOpenArray,yukimiBorderArray:action.yukimiBorderArray,message:messages.CLEAR,card:action.card});
      case types.HIDE_START_MODAL:
        return Object.assign({}, state, {gameStarted:true, okBtnOpen: "none",resetBtnOpen: "block",yukimiOpenArray:initArrays.initOpenArray.slice(),message:messages.GAMING});
      case types.HIDE_MISS_MODAL:
        return Object.assign({}, state, {missModalIsOpen: action.missModalIsOpen,selected: action.selected,yukimiOpenArray:action.yukimiOpenArray,message:messages.GAMING});
    default:
        return state
    }
};




