import { types,border,initArrays } from './reducer'

/*
  Action Creator:
  Appコンポーネント (src/App.js) から呼ばれる Action Creator群
  App.jsから呼ばれ、Action Creator内部で生成されたオブジェクトはreducer (./reducers) に渡される。
*/

//addText (Action Creator) : Appコンポーネント (App.js) でAddボタンを呼び出した時に呼ばれる。
// type (何のアクションなのかを示す定数であり必須) と下記関数はApp.jsから渡されたテキストを包含したオブジェクトを return する。return されたオブジェクトは後述のReducerに渡される。

export class PeroAction{
}
PeroAction.peroActions = {
    gameStart: function gameStart (state,intervalID) {
        let initArray = initArrays.initCardArray.slice();
        for(var i = initArray.length - 1; i > 0; i--){
            var r = Math.floor(Math.random() * (i + 1));
            var tmp = initArray[i];
            initArray[i] = initArray[r];
            initArray[r] = tmp;
        }
        for(var j = 0; j <8; j++){
            initArray[j] = initArray[j+8];
        }
        for(var k = initArray.length - 1; k > 0; k--){
            var r2 = Math.floor(Math.random() * (k + 1));
            var tmp2 = initArray[k];
            initArray[k] = initArray[r2];
            initArray[r2] = tmp2;
        }
        return {
            type: types.GAME_START,
            intervalID: intervalID,
            startModalIsOpen: true,
            yukimiArray:initArray
        }
    },
    
    updateTimerAction: function updateTimerAction () {
        return {type: types.UPDATE_TIMER}
    },

    gameReset: function gameReset() {
        return {
            type: types.GAME_RESET,
        };
    },

    imgSelected: function imgSelected(num, state) {
        let yukimiOpenArrayTmp = state.yukimiOpenArray;
        let yukimiBorderArrayTmp = state.yukimiBorderArray;
        yukimiOpenArrayTmp[num] = true;
        if(!state.selected){
            yukimiBorderArrayTmp[num] = border.SELECTED;
            return async (dispatcher) => {
                dispatcher({
                    type: types.ONE_SELECTED,
                    selected: true,
                    selectedNum: num,
                    yukimiOpenArray:yukimiOpenArrayTmp,
                    yukimiBorderArray:yukimiBorderArrayTmp
                });
            }
        } else {
            if(state.yukimiArray[num].name === state.yukimiArray[state.selectedNum].name){
                yukimiBorderArrayTmp[num] = border.OK;
                yukimiBorderArrayTmp[state.selectedNum] = border.OK;
                if(state.okCount < 7){
                    return async (dispatcher) => {
                        await dispatcher({
                            type: types.OK,
                            okModalIsOpen: true,
                            selected: false,
                            selectedNum: num,
                            yukimiOpenArray:yukimiOpenArrayTmp,
                            yukimiBorderArray:yukimiBorderArrayTmp,
                            card:state.yukimiArray[num]
                        });
                    }
                } else{
                    return async (dispatcher) => {
                        await dispatcher({
                            type: types.CLEAR,
                            clearModalIsOpen: true,
                            selected: false,
                            selectedNum: num,
                            yukimiOpenArray:yukimiOpenArrayTmp,
                            yukimiBorderArray:yukimiBorderArrayTmp,
                            card:state.yukimiArray[num],
                            tweet:"clear message make"
                        });
                    }                    
                }
            } else{
                yukimiBorderArrayTmp[num] = border.SELECTED;
                return async (dispatcher) => {
                    dispatcher({
                    type: types.MISS,
                    missModalIsOpen: true,
                    selected: false,
                    selectedNum: num,
                    selectedNum2: state.selectedNum,
                    yukimiOpenArray:yukimiOpenArrayTmp,
                    yukimiBorderArray:yukimiBorderArrayTmp,
                });
                }
            }
        }
    },
    hideStartModal: function hideStartModal() {
        return {
            type: types.HIDE_START_MODAL,
            startModalIsOpen: false,
        };
    },
    hideMissModal: function hideMissModal(state) {
        let yukimiOpenArrayTmp = state.yukimiOpenArray;
        yukimiOpenArrayTmp[state.selectedNum] = false;
        yukimiOpenArrayTmp[state.selectedNum2] = false;
        let yukimiBorderArrayTmp = state.yukimiBorderArray;
        yukimiBorderArrayTmp[state.selectedNum] = border.NONE;
        yukimiBorderArrayTmp[state.selectedNum2] = border.NONE;
        return {
            type: types.HIDE_MISS_MODAL,
            missModalIsOpen: false,
            selected: false,
            yukimiOpenArray:yukimiOpenArrayTmp,
            yukimiBorderArray:yukimiBorderArrayTmp
        };
    },

}



