    //start ボタンをクリックした時に呼び出される
    onStartBtnClicked(e) {
      this.props.gameStart(this.props);
    }
    onResetBtnClicked(e) {
      this.props.peroActions.gameReset()
    }
    onImgClicked(e,num) {
      if(this.props.gameStarted){
        this.props.peroActions.imgSelected(num,this.props)
      }
    }
    onOkBtnClicked(e) {
      this.props.peroActions.hideStartModal()
    }
    hideMissModal(e) {
      this.props.peroActions.hideMissModal(this.props)
    }

