function mapStateToProps(state) {
    return {
        message:state.message,
        count: state.count,
        intervalID: state.intervalID,
        gameStarted: state.gameStarted,
        startBtnOpen: state.startBtnOpen,
        okBtnOpen: state.okBtnOpen,
        resetBtnOpen: state.resetBtnOpen,
        okModalOpen: state.okModalOpen,
        clearModalOpen: state.clearModalOpen,
        missModalIsOpen: state.missModalIsOpen,
        selected: state.selected,
        selectedNum: state.selectedNum,
        selectedNum2: state.selectedNum2,
        okCount: state.okCount,
        yukimiArray:state.yukimiArray.slice(),
        yukimiOpenArray:state.yukimiOpenArray.slice(),
        yukimiBorderArray:state.yukimiBorderArray.slice(),
        card:state.card,
        tweet:state.tweet,
        hashtags:state.hashtags
      };
  }
    
  function mapActionToProps(dispatch) {
    return {
        peroActions:bindActionCreators(PeroAction.peroActions, dispatch),
        gameStart: (state) => {
          const intervalID = setInterval(() => dispatch(PeroAction.peroActions.updateTimerAction()), 1000)
          dispatch(PeroAction.peroActions.gameStart(state,intervalID))
        },
    };
  }
  const customStyles = {
    overlay: {
      position: 'fixed',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      backgroundColor: 'rgba(255, 255, 255, 0)'
    },
    content : {
      position: 'absolute',
      top                   : '20%',
      left                  : '20%',
      right                 : 'auto',
      bottom                : 'auto',
      marginTop:'0px',
      padding: '0px',
      border: '0px solid #ccc',
    }
  };

// Appコンポーネントを connect() メソッドでラップした上でエクスポート
// 第一引数には上述のselector() メソッドを使って、stateの中からコンポーネントが必要とするデータを指定、第二引数にはコンポーネント自体を指定。その上でエクスポート。
export default connect(mapStateToProps,(dispatch) => mapActionToProps)(App)


