import React from 'react'
import { render } from 'react-dom'
import { createStore, applyMiddleware } from 'redux'
import { Provider }  from 'react-redux'
import thunk  from 'redux-thunk'

/* Appコンポーネントのインポート */
import App from './App'
/* Reducerから Store をインポート */
import { reducer, initialState } from './reducer'

/*
  Entry Point:
   - Appコンポーネント (App.jsからインポート) をProviderコンポーネント (react-reduxからインポート) でラップする
   - さらに、createStore() メソッドで生成した Store をProviderコンポーネントに設定する
*/

//createStore() メソッドでアプリケーションのStateを保存する Store を生成する。
const createStoreWithMiddleware = applyMiddleware(thunk)(createStore);
let applicationStore = createStoreWithMiddleware(reducer,initialState);
let rootElement = document.querySelector('#root');

render (
    //Providerに作成したstoreをセット
    <Provider store={applicationStore}>
        <App />
    </Provider>,
    // rootElement下にコンポーネントを生成
    rootElement
);


